tress because it takes forever then for a player to be \n                  ready and that means lost time during a game. Players, though, are not the only ones with problems. Game masters also have \n                  to go through alot of steps to get a game ready from prepping maps and tokens to figuring out what they’re going to run for \n                  a game. With many different places to find information it isn’t easy for players or game masters to be 100% ready for a game.")]),_c('p',[_vm._v("The main problem comes down to: How can a complex tabletop system be made easy to use for both players and game masters?")]),_vm._m(3),_c('img',{attrs:{"src":"/img/venture/CompetetiveAnalysis.png","id":"analysis"}}),_vm._m(4),_c('img',{attrs:{"src":"/img/venture/affinitygraph.png","id":"affinity"}}),_vm._m(5),_c('img',{staticClass:"image-size",attrs:{"src":"/img/venture/Storyboard.png"}}),_vm._m(6),_c('img',{staticClass:"image-size",attrs:{"src":"/img/venture/HighExample/PaperPrototype3.png"}}),_c('img',{staticClass:"image-size",attrs:{"src":"/img/venture/HighExample/Game Board Page.png"}}),_c('img',{staticClass:"image-size",attrs:{"src":"/img/venture/HighExample/Game Board Page High.png"}}),_vm._m(7),_c('p',[_vm._v("The things I thought were great, were not! But that was completely perfect. The feedback was englightening and made me \n                  rethink a lot of the design choices I had made. Little known fact, I like iterating on my wireframes!")]),_c('p',[_vm._v("The main feedback: “The widgets on the game board are too small. I don’t feel like I’ll have enough space to see all of the \n                  important information, especially on the map.”")]),_c('p',[_vm._v("Normally, a table top game map on a Virtual Table Top system spans the whole screen. It allows the user to pan around the \n                  map and zoom in and zoom out. My original idea was to ax that as I was going to have other elements that could be positioned\n                   around the page and I didn’t want the user to feel like everything was overlapping too much. However, table top players like\n                    their large maps. So I axed my own idea and changed it so that user could resize their map, as long as it wasn’t too small,\n                     and allow them to collapse and expand other widgets to give them optimal viewing space.")]),_c('p',[_vm._v("Next important feedback: “The bar at the top of the game board screen isn’t clear that it means a new window will be opened\n                   up. In that aspect, opening a new window would be the same as putting a bar up top that allows the user to change their \n                   settings. By the way, why can’t I change my game board settings here?”")]),_c('p',[_vm._v("This was a very valid point. I had only given the user the option to change their game settings outside of the game board. \n                  That makes no sense! Each game would have their own settings based on the user’s preference for that game’s play style. Maybe \n                  in one game the user isn’t using video at all and as such wouldn’t even need the video widget anymore. This also brought to \n                  light, in pairing with the previous feedback, that the Data Library widget was going to suffer if left on the game board. \n                  There was going to be too much information to be displayed in a widget. So I changed the idea of new window, gave the user a \n                  dropdown bar that allows them to open a new window with the data library, change their settings and exit the game board. \n                  Exiting the game board would return the user to their Games page instead of closing the application completely.")]),_vm._m(8),_vm._m(9),_vm._m(10),_vm._m(11),_vm._m(12),_vm._m(13),_vm._m(14),_vm._m(15),_vm._m(16),_c('br'),_c('h4',[_vm._v("Other Works")]),_c('router-link',{attrs:{"to":"/works/streetsmarts"}},[_c('img',{staticClass:"border-radius project-view",attrs:{"